:root{
  --bg: #f2f2f2;
  --text: #222;
  --muted: #616161;
  --accent: #007aff;
  --accent-600: #0058d6;
  --card: rgba(255,255,255,0.6);
  --glass-tint: rgba(255,255,255,0.35);
  --border: rgba(255,255,255,0.4);
  --shadow: 0 8px 30px rgba(0,0,0,0.10);
  --radius: 14px;
  --safe-bottom: env(safe-area-inset-bottom, 12px);
  --max-chat-width: 880px;
  --min-chat-width: 260px;
}

/* Font */
@font-face{
  font-family: 'InterVar';
  src: local('Inter'), local('InterVar'), local('System');
  font-weight: 100 900;
  font-style: normal;
  font-display: swap;
}
* { box-sizing: border-box; }

body{
  font-family: "InterVar", -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  margin:0;
  padding:0;
  background: var(--bg);
  color: var(--text);
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale;
  -webkit-text-size-adjust:100%;
}

/* Links */
a{ color:var(--accent); text-decoration:none; }
a:hover{ color:var(--accent-600); }

/* Headings & text */
h1,h2,h3,h4,h5,h6{ margin:0 0 12px 0; font-weight:700; color:var(--text); }
p{ color:var(--muted); margin:0 0 12px 0; line-height:1.6; }

/* Images */
img{ width:100%; height:auto; border-radius:10px; display:block; }

/* Container */
.container{
  max-width:1100px;
  margin:36px auto;
  padding:20px;
  background: #fff;
  border-radius:12px;
  box-shadow: 0 6px 18px rgba(0,0,0,0.05);
}

/* Header & nav */
header{ background:#3498db; color:#fff; padding:18px; text-align:center; border-radius:10px 10px 0 0; }
header h1{ font-size:24px; margin:0; font-weight:700; }
nav ul{ list-style:none; margin:12px 0 0 0; padding:0; display:flex; gap:12px; flex-wrap:wrap; justify-content:center; }
nav a{ color:#fff; }

/* Articoli layout */
.articoli{ display:flex; flex-wrap:wrap; gap:20px; margin-top:20px; justify-content:space-between; }
.articolo{
  padding:18px;
  background:#fbfbfb;
  border:1px solid #eee;
  border-radius:12px;
  width: calc(50% - 10px);
  box-shadow: 0 2px 8px rgba(0,0,0,0.02);
  font-family: "InterVar", -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}
.articolo h2{ font-size:18px; margin-top:0; color:var(--text); }
.articolo p{ color:var(--muted); font-size:15px; }

@media (max-width:640px){
  .articolo{ width:100%; padding:14px; }
  .articolo p{ font-size:16px; }
}

/* ==========================
IA-CHAT: Frosted glass iOS-style + responsive, prevents overflow
========================== */

/* Mobile-first: fluid, centered, with safe-area support */
.ia-chat{
  position: fixed;
  left: env(safe-area-inset-left, 12px);
  right: env(safe-area-inset-right, 12px);
  bottom: calc(env(safe-area-inset-bottom, 12px) + 8px);
  margin: 0 auto;
  width: auto;
  max-width: calc(100% - 24px); /* sempre margine ai lati */
  min-width: var(--min-chat-width);
  box-sizing: border-box;
  background: linear-gradient(180deg, rgba(255,255,255,0.55), rgba(255,255,255,0.40));
  border-radius: var(--radius);
  padding: 10px;
  box-shadow: var(--shadow);
  z-index: 9999;
  display: grid;
  gap: 10px;
  transition: transform 220ms cubic-bezier(.2,.8,.2,1), opacity 180ms ease, visibility 180ms;
  transform-origin: center bottom;
  backdrop-filter: blur(10px) saturate(120%);
  -webkit-backdrop-filter: blur(10px) saturate(120%);
  border: 1px solid rgba(255,255,255,0.25);
  will-change: transform, opacity;
  overflow: hidden; /* evita overflow esterno */
  touch-action: manipulation;
}

/* Hidden state */
.ia-chat.hidden{
  transform: translateY(120%);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

/* Forza contenuti interni a non superare la larghezza */
.ia-chat, .ia-chat *{
  max-width: 100%;
  box-sizing: border-box;
  word-break: break-word;
}

/* Inner: contenimento e controllo altezza */
.ia-chat .inner{
  max-height: 72vh;
  overflow: hidden;
  display:flex;
  flex-direction:column;
  gap:8px;
}

/* Chat window scrollabile verticalmente, con overflow-x nascosto */
.ia-chat .chat-window{
  overflow-y:auto;
  overflow-x:hidden;
  padding:8px;
  background: linear-gradient(180deg, rgba(255,255,255,0.08), transparent);
  border-radius:10px;
  border: 1px solid rgba(0,0,0,0.04);
  max-height: 44vh; /* conservativo per evitare sovrapposizione tastiera */
  -webkit-overflow-scrolling: touch;
}

/* Adatta altezza chat quando è presente la classe compact (es. keyboard visible) */
.ia-chat.compact .inner{ max-height: 56vh; }
.ia-chat.compact .chat-window{ max-height: 32vh; }

/* Messages styling */
.ia-chat .chat-window .msg{ margin-bottom:10px; line-height:1.45; word-wrap: break-word; }
.ia-chat .chat-window .msg.user{ font-weight:600; color:var(--accent); display:inline-block; min-width:48px; }
.ia-chat .chat-window .msg.bot{ color:var(--text); }

/* Panel (input + controls) pinned and responsive */
.ia-chat .panel{
  display:flex;
  gap:8px;
  align-items:center;
  width:100%;
  flex-shrink:0;
}

/* Input row */
.ia-chat input[type="text"]{
  flex:1;
  padding:10px 12px;
  border-radius:10px;
  border:1px solid rgba(0,0,0,0.06);
  background: rgba(255,255,255,0.7);
  font-size:15px;
  color:var(--text);
  backdrop-filter: blur(4px);
  min-width:0; /* importante per evitare overflow flex */
}
.ia-chat .btn{
  padding:10px 12px;
  border-radius:10px;
  border:none;
  background: linear-gradient(180deg,var(--accent),var(--accent-600));
  color:#fff;
  cursor:pointer;
  font-weight:700;
  display:inline-flex;
  align-items:center;
  justify-content:center;
  min-width:44px;
  height:44px;
}
.ia-chat .btn.secondary{
  background: rgba(255,255,255,0.6);
  color:var(--text);
  border:1px solid rgba(0,0,0,0.04);
}

/* Controls row */
.ia-chat .controls{
  display:flex;
  gap:8px;
  align-items:center;
  width:100%;
  flex-wrap:wrap;
}
.ia-chat .controls .rescan-btn{
  padding:8px 10px;
  border-radius:8px;
  border:1px solid rgba(0,0,0,0.06);
  background: rgba(255,255,255,0.5);
  cursor:pointer;
  font-size:14px;
}
.ia-chat .controls .counter{ margin-left:auto; color:var(--muted); font-size:13px; }

/* Suggestions / results */
.ia-chat .results{ font-size:14px; }
.ia-chat .results ul{ margin:6px 0 0 14px; padding:0; list-style:disc; }
.ia-chat .results li{
  margin-bottom:8px;
  padding:8px;
  border-radius:8px;
  background: rgba(0,0,0,0.03);
  cursor:pointer;
}

/* Make elements more touch-friendly on small screens */
@media (max-width:480px){
  .ia-chat{ left:8px; right:8px; padding:8px; min-width:unset; max-width: calc(100% - 16px); }
  .ia-chat .chat-window{ max-height:40vh; }
  .ia-chat .btn{ min-width:40px; height:40px; padding:8px; }
  .ia-chat .results li{ padding:10px; }
}

/* Desktop/tablet layout: fixed width at right, avoid overflow by constraining width */
@media (min-width:860px){
  .ia-chat{
    left:auto;
    right:16px;
    bottom: calc(16px + var(--safe-bottom));
    width: 380px;
    max-width: 42vw;
    min-width: 300px;
    padding:12px 14px;
    overflow: visible;
  }
  .ia-chat .inner{ max-height: 72vh; }
  .ia-chat .chat-window{ max-height: 60vh; }
}

/* Very wide screens */
@media (min-width:1200px){
  .ia-chat{ width: min(480px, 36vw); }
}

/* Toggle button */
.ia-toggle{
  position:fixed;
  right: calc(12px + var(--safe-bottom) / 6);
  bottom: calc(12px + var(--safe-bottom));
  z-index:10000;
  width:56px;
  height:56px;
  border-radius:50%;
  display:inline-flex;
  align-items:center;
  justify-content:center;
  background: linear-gradient(180deg,var(--accent),var(--accent-600));
  color:#fff;
  border:none;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
  cursor:pointer;
  font-size:22px;
}
.ia-chat:not(.hidden) + .ia-toggle,
.ia-chat-visible + .ia-toggle{ opacity:0; pointer-events:none; transform:scale(0.9); }

/* Accessibility focus */
.ia-chat button:focus, .ia-chat input:focus, .ia-toggle:focus{
  outline:3px solid rgba(0,122,255,0.14);
  outline-offset:2px;
  border-radius:10px;
}

/* Reduce motion */
@media (prefers-reduced-motion: reduce){
  .ia-chat, .ia-toggle, .ia-chat .results li { transition:none; }
}

/* Utility: force hide */
.chat-hidden .ia-chat{ transform: translateY(120%); opacity:0; visibility:hidden; pointer-events:none; }

/* Optional JS-friendly helpers (no JS included here):
   - add class "compact" to .ia-chat when keyboard is visible to reduce heights
   - ensure any long horizontal content (images, tables) uses max-width:100% */

